		text-align: center;
			
	}

	.imageTipe{
		background-attachment:scroll;
		background-clip:border-box;
		background-color:rgba(0, 0, 0, 0);
		background-image:url(file:///C:/Users/Laboratoria/Desktop/PortfolioProyect/assets/css/images/overlay.png);
		background-origin:padding-box;
		background-position-x:0%;
		background-position-y:0%;
		background-repeat-x:;
		background-repeat-y:;
		background-size:auto;
		border-bottom-left-radius:5.6px;
		border-bottom-right-radius:5.6px;
		border-top-left-radius:5.6px;
		border-top-right-radius:5.6px;
		box-sizing:border-box;
		content:"";
		display:block;
		height:207.313px;
		left:0px;
		opacity:0.5; 
		top:0px;
		transition-delay:0s;
		transition-duration:0.2s;
		transition-property:opacity;
		transition-timing-function:ease-in-out;
		width:353.5px;
	}

	
